import React from "react";
import "./About.scss";
import Medias from '../../Medias/Medias'

export default function About({mediasData}) {
  return (
    <div className="about">
      <div>
        <h1>ukasz Cielak</h1>
        <h2>Junior Front-end Developer</h2>
        <h2> Junior Tester</h2>
        <p>
          Po ukoczonym kursie Tester
          oprogramowania zaczem pasjonowa si programowaniem, aktualnie
          poszerzam swoj wiedz i doskonal umiejtnoci w jzyku JavaScript. Ten projekt jest czci tego, czego do tej pory si nauczyem.
          Chciabym wykorzysta swoj wiedz w praktyce oraz dalej si rozwija. Dobrze odnajduje si w pracy zespoowej jak i w indywidualnych
          zadaniach. Naucz si wszystkiego, co bdzie potrzebne by wnie
          wiksz warto w swj powicony czas.</p><p>
          Zapraszam do kontaktu przez linkedin oraz do sprawdzenia mojego profilu na githubie, ktry bd stopniowo uzupenia o nowe projekty.
        </p>
        <div className='media_about'>
        {mediasData.map((el=> <Medias el={el} />))}
        </div>
      </div>
      <img src="/assets/images/IMG_20191019_221930.jpg" />
    </div>
  );
}
